.gallery {
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}
  
.item {
    border-radius: 2px;
}
  
.img {
    width: 100%;
    height: 260px;
    object-fit: cover;
}
  
.img:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}